/* -------  Event  ------- */
.event {
  width: 90%;
  height: 8em;
  margin-top: 6vh; }

@media only screen and (min-width: 667px) {
  .event {
    height: 9em;
    margin-top: 9vh; } }

@media only screen and (min-width: 999px) {
  .event {
    height: 10em;
    margin-top: 12vh; } }

@media only screen and (min-width: 1331px) {
  .event {
    height: 11em;
    margin-top: 15vh; } }



