.container {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center; }

.link {
  line-height: 1;
  margin: 0 0.66em;
  padding: 1%;
  position: relative; }

.iconOutline {
  display: inline-block; }

.iconFull {
  opacity: 0;
  position: absolute;
  transition: opacity 0.44s;
  composes: iconOutline; }

.link:hover .iconFull {
  opacity: 1; }



