/* -------  Panel  ------- */
.item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  cursor: pointer;
  transition: background 0.44s;
  composes: flexy from 'styles/_utils.scss'; }

.item:not(:last-child) {
  border-right: 1px solid #656565; }

.itemActive {
  background: #f5f5f5;
  composes: item; }

@media (hover) {
  .item:hover {
    background: #f5f5f5; } }



