/* -------  colors  ------- */
/* ------------------------ */
/* ---  media queries  ---- */
/* ------------------------ */
/* -------  Header  ------- */
.container {
  background: #f5f5f5;
  color: #070707;
  width: 100%;
  font-size: 1.2em;
  z-index: 10; }

.menuOpen {
  composes: container; }

.innerContainer {
  height: 60px;
  padding: 0 2%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

/* -------  Logo  ------- */
.logo {
  display: block;
  height: 48px;
  padding: 1.2%; }

/* -------  Trigger  ------- */
.trigger {
  width: 52px;
  height: 52px;
  position: relative;
  cursor: pointer; }

.triggerLine {
  height: 2px;
  width: 24px;
  display: block;
  background: #070707;
  position: absolute;
  top: 25px;
  left: 14px;
  transition: -webkit-transform 0.22s;
  transition: transform 0.22s;
  transition: transform 0.22s, -webkit-transform 0.22s; }

.triggerTop {
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
  composes: triggerLine; }

.triggerBottom {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  composes: triggerLine; }

.menuOpen .triggerTop {
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg); }

.menuOpen .triggerBottom {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg); }

/* -------  Menu  ------- */
.menu {
  background: #ffffff;
  overflow: hidden;
  transition: max-height 0.22s; }

.list {
  padding: 0.75em 1em 0; }

.item, .extra {
  margin-bottom: 0.4em; }

.extra:last-child {
  margin-bottom: 0.2em; }

.block {
  display: block;
  height: 0.5em; }

.link {
  display: block;
  padding-top: 0.4em; }

.link::after, :global(.active::after) {
  content: '';
  display: block;
  height: 1px;
  background-image: linear-gradient(to right, black, transparent); }

.extra .link {
  font-size: 85%;
  letter-spacing: -1px; }

.icon {
  content: '';
  display: inline-block;
  height: 1em;
  width: 1.5em;
  margin: 0.2em 0.2em 0 0;
  float: left;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center; }

/* -------  Footer  ------- */
.footer {
  height: 2em;
  font-size: 1.6em;
  color: #070707; }

@media only screen and (max-width: 666px) {
  /* -------  Menu  ------- */
  .text {
    display: inline-block;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition: -webkit-transform 0.22s;
    transition: transform 0.22s;
    transition: transform 0.22s, -webkit-transform 0.22s; } }

@media only screen and (max-width: 666px) and (hover) {
  .link:hover .text {
    -webkit-transform: translateX(10%);
            transform: translateX(10%); } }

@media only screen and (min-width: 550px) and (max-width: 667px) {
  /* -------  Header  ------- */
  .container {
    font-size: 1.25em; } }

@media only screen and (min-width: 667px) {
  /* -------  Header  ------- */
  .container {
    line-height: 1;
    height: 60px;
    padding: 0 2%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-negative: 0;
        flex-shrink: 0; }
  .innerContainer {
    padding: 0; }
  /* -------  Trigger  ------- */
  .trigger {
    display: none; }
  /* -------  Menu  ------- */
  .menu {
    background: initial;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    max-width: 667px; }
  .list {
    padding: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center; }
  .item {
    margin: 6px 1% 0; }
  .extra {
    margin: 6px 0.5% 0; }
  .extra:last-child {
    margin-bottom: inherit; }
  .link {
    padding-top: 0;
    position: relative; }
  .link::after, :global(.active::after) {
    width: 0;
    position: absolute;
    transition: width 0.22s; }
  :global(.active::after) {
    width: 100%; } }

@media only screen and (min-width: 667px) and (hover) {
  .link:hover::after {
    width: 100%; } }

@media only screen and (min-width: 667px) {
  .block {
    height: 30px;
    width: 2px;
    background: #070707;
    margin: 0 1%; }
  .link {
    display: block;
    padding: 4px 0; }
  .link::after {
    bottom: 2px; }
  .link:hover .text {
    transition: initial; } }



